import React, { useEffect, useRef, useState } from "react";
import {
  TextField,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Typography,
  Autocomplete,
  Box,
  Link,
  Card,
  Button,
} from "@mui/material";
import { HeaderBox, InfoBox } from "./HeaderBox";
import { RightAlignedButton } from "./DynamicSearch";

interface Selection {
  [key: string]: string;
}

interface Detail {
  name: string;
  value: string;
  isFilter: boolean;
  dataType: string;
  selections: Selection;
  fieldType: number;
}

interface Section {
  title: string;
  description: string;
  columnType: number;
  details: Detail[];
}

interface SearchComponentProps {
  data: Section[];
  setValues: any; // Consider using a more specific type for setValues
}

const SearchFilters: React.FC<SearchComponentProps> = ({ data, setValues }) => {
  const [filterValues, setFilterValues] = useState(() => {
    const initialFilterValues = {};
    data.forEach((section) => {
      section.details.forEach((detail) => {
        if (detail.isFilter) {
          initialFilterValues[detail.value] = "";
        }
      });
    });
    return initialFilterValues;
  });

  const handleChange = (name: string, value: string) => {
    setFilterValues((prevValues) => ({
      ...prevValues,
      [name]: value,
    }));
    setValues((prevValues) => ({
      ...prevValues,

      [name]: value,
    }));
  };

  if (!data.length) return <></>;
  return (
    <Box
      style={{
        minWidth: "300px",
        paddingBottom: "20px",
        height: "100%",
      }}
    >
      {data.map((section, sectionIndex) => (
        <React.Fragment key={sectionIndex}>
          <Card sx={{ marginBottom: 1 }}>
            <Box sx={{ border: "2px solid #eeeeee" }}>
              <HeaderBox
                style={{
                  height: "4vh",
                }}
              >
                <InfoBox>
                  <Typography variant="overline">{section.title}</Typography>
                </InfoBox>
              </HeaderBox>

              {section.details
                .filter((detail) => detail.isFilter)
                .map((detail, index) => {
                  switch (detail.fieldType) {
                    case 1: // Text Input
                      const options = Object.values(detail.selections);
                      return (
                        <Autocomplete
                          key={index}
                          freeSolo
                          options={options}
                          onChange={(ev: any, value: string | null) => {
                            handleChange(detail.name, value || "");
                          }}
                          onInputChange={() => {
                            handleChange(detail.name, "");
                          }}
                          style={{ padding: "10px" }}
                          renderInput={(params) => (
                            <TextField
                              {...params}
                              label={detail.name}
                              variant="outlined"
                              value={filterValues[detail.value] ?? ""}
                              onChange={(ev) => {
                                handleChange(detail.name, ev.target.value);
                              }}
                              sx={{
                                "& .MuiOutlinedInput-root": {
                                  "& fieldset": {
                                    borderColor: "#e0e0e0",
                                    borderWidth: "1px",
                                  },
                                  "&:hover fieldset": {
                                    borderColor: " #bdbdbd ",
                                  },
                                  "&.Mui-focused fieldset": {
                                    borderColor: "#bdbdbd",
                                  },
                                },
                              }}
                            />
                          )}
                        />
                      );
                    case 6: // Numeric Input
                      return (
                        <TextField
                          style={{ padding: "10px" }}
                          key={index}
                          type="number"
                          label={detail.name}
                          variant="outlined"
                          fullWidth
                          value={`${filterValues[detail.value]}` || ""}
                          onChange={(ev) => {
                            handleChange(detail.value, ev.target.value);
                          }}
                          sx={{
                            "& .MuiOutlinedInput-root": {
                              "& fieldset": {
                                borderColor: "#e0e0e0",
                                borderWidth: "1px",
                              },
                              "&:hover fieldset": {
                                borderColor: " #bdbdbd ",
                              },
                              "&.Mui-focused fieldset": {
                                borderColor: "#bdbdbd",
                              },
                            },
                            marginBottom: 1,
                            top: 15,
                          }}
                        />
                      );
                    case 8: // Dropdown
                      const val = Object.entries(filterValues).find(
                        ([key, value]) => key === detail.value
                      );
                      let value = "";
                      if (!val) {
                        value = filterValues[detail.value];
                      } else {
                        value = val[1] as any;
                      }

                      return (
                        <FormControl
                          fullWidth
                          key={index}
                          style={{ padding: "10px" }}
                          sx={{
                            "& .MuiOutlinedInput-root": {
                              "& fieldset": {
                                borderColor: "#e0e0e0",
                                borderWidth: "1px",
                              },
                              "&:hover fieldset": {
                                borderColor: " #bdbdbd ",
                              },
                              "&.Mui-focused fieldset": {
                                borderColor: "#bdbdbd",
                              },
                            },
                          }}
                        >
                          <InputLabel sx={{ padding: "0.7rem 0.5rem" }}>
                            {detail.name}
                          </InputLabel>
                          {/* @ts_ignore */}
                          <Select
                            value={value}
                            label={detail.name}
                            onChange={(ev) => {
                              const newValue = ev.target.value as string;
                              if (detail.dataType === "number") {
                                handleChange(detail.value, `${newValue}`);
                              } else {
                                handleChange(detail.value, newValue);
                              }
                            }}
                          >
                            {Object.entries(detail.selections).map(
                              ([key, value]) => (
                                <MenuItem key={key} value={key}>
                                  {value}
                                </MenuItem>
                              )
                            )}
                          </Select>
                        </FormControl>
                      );
                    default:
                      return null;
                  }
                })}
            </Box>
          </Card>
        </React.Fragment>
      ))}
    </Box>
  );
};

export default SearchFilters;
